import request from '@/utils/request'

export function DownloadFile(url){
    return request({
        url : url,
        method: 'get',
    })
}

export function UpdateLicense(data){
    return request({
        url: '/supermarket/updateLicense',
        method: 'post',
        data : data,
        headers: {
            'Content-Type': 'multipart/form-data'
        }
    })
}

export function SupermarketList(data){
    return request({
        url : '/supermarket/list',
        method : 'get',
        params : data
    })
}
