<template>
    <div class="user-container">
        <el-form  ref="form" :model="form" label-width="120px" autocomplete="on">
      <el-form-item label="用户名" prop="username">
        <el-input ref="username" placeholder="用户名" v-model="form.user.username" disabled/>
      </el-form-item>
      <el-form-item label="密码"  prop="password" aria-disabled="">
        <el-input ref="password"  placeholder="密码" v-model="form.user.password"  disabled/>
      </el-form-item>
      <el-form-item label="邮箱"  prop="email">
        <el-input ref="email" placeholder="**@163.com,***@qq.com" v-model="form.user.email" disabled/>
      </el-form-item>
      <el-form-item label="电话号码"  prop="phone">
        <el-input placeholder="请输入电话号码" v-model="form.user.phone" disabled/>
      </el-form-item>
      <el-form-item label="公司"  prop="company">
        <el-input v-model="form.company.companyName" disabled/>
      </el-form-item>
      <el-form-item label="超市名称"  prop="sname">
        <el-input placeholder="超市名称" v-model="form.supermark.sname" disabled />
      </el-form-item>
      <el-form-item label="超市详细地址"  prop="sdetail">
        <el-input placeholder="超市详细地址" v-model="form.supermark.detail" disabled/>
      </el-form-item>
      <el-form-item label="地区"  prop="saddress">
        <!-- <addressSelect ref="addressSelect" /> -->
        <el-input v-model="form.supermark.address" disabled/>
      </el-form-item>
      <el-form-item label="证件"  prop="license">
        <span>{{this.form.supermark.license.name}}&nbsp;</span>
        <el-button class="upload-demo" @click="Preview">预览</el-button>
      </el-form-item>
    </el-form>
    <el-button type="primary" @click="PasswordFormVisible = true">修改密码</el-button>
    <el-button type="primary" @click="LicenseFormVisible = true">更新证件信息</el-button>
    </div>

    <!---修改密码表单--->
    <el-dialog v-model="PasswordFormVisible"  title="修改密码">
    <el-form ref="passwordform" :model="passwordform" :rules="passwordRules">
      <el-form-item label="新密码" prop="password">
        <el-input v-model="passwordform.password"  show-password/>
      </el-form-item>
      <el-form-item label="再次确认密码" prop="mdfpassword">
        <el-input v-model="passwordform.mdfpassword"  show-password/>
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="PasswordFormVisible = false">返回</el-button>
        <el-button type="primary" @click="UpdatePassword">
          修改密码
        </el-button>
      </span>
    </template>
  </el-dialog>

  <!--更新证件信息表单--->
  <el-dialog v-model="LicenseFormVisible"  title="更新证件">
    <el-form ref="Licenseform" :model="Licenseform" :rules="LicenseRules">
        <el-form-item label="证件上传"  prop="license">
        <el-upload
          ref="upload"
          class="upload-demo"
          :limit="1"
          :on-change="submitUpload"
          :auto-upload="false"
          
        >
        <template #trigger>
          <el-button type="primary">选择文件</el-button>
        </template>
        </el-upload>
      </el-form-item>
      </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="LicenseFormVisible = false">返回</el-button>
        <el-button type="primary" @click="UpdateLicense">
          更新证件信息
        </el-button>
      </span>
    </template>
  </el-dialog>
</template>
<script>
import addressSelect from '@/components/AddressSelect'
import {CompanyList} from '@/api/company'
import {getMessage,UpdatePassword} from '@/api/user'
import {DownloadFile,UpdateLicense} from '@/api/supermarket'
import { ElMessage} from 'element-plus'

export default {
    components:{
        addressSelect
    },
    created(){
        this.getCompanyList()
        this.LoadUserMessage()
    },
    mounted() {
    if (this.form.username === '') {
      this.$refs.username.focus()
    } else if (this.form.password === '') {
      this.$refs.password.focus()
    } else if (this.form.mdfPassword === ''){
      this.$refs.mdfPassword.focus()
    } else if (this.form.email == ''){
      this.$refs.email.focus()
    }
  },
    // computed:{
      
    // },
    data(){
    //密码格式校验    
    const validatePassword = (rule,value,callback) =>{
      var pwd = this.passwordform.password
      if(pwd.trim() === '' || pwd.length < 6){
        //函数回调
        callback(new Error('密码不能为空且不得少于6个字符'))
      }else{
        callback()
      }
    }

    const validatemdfPassword = (rule,value,callback) =>{
        var pwd = this.passwordform.password
        var mdf = this.passwordform.mdfpassword
        if(pwd != mdf){
            callback(new Error('两次输入的密码不一致'))
        }else{
            callback()
        }
    }

    //文件校验
    const validateLicense = (rule,value,callback) =>{
      if(Object.keys(this.Licenseform.license).length === 0){
        callback(new Error("证件信息不能为空"))
      }else{
        callback()
      }
    }
    return {
        PasswordFormVisible: false,
        LicenseFormVisible: false,
        companies:{},
        form:{
            user :{
                username: '',
                password: '',
                email: '',
                phone: '',
              },
            company:{
                companyId: '',
                companyName : '',
            },
            supermark:{
                sname: '',
                detail: '',
                address: '',
                license: {
                    name: '',
                    url: '',
                }
              } 
            
            },
        passwordform: {
            password: '',
            mdfpassword: '',
        },
        Licenseform:{
            license: {}
        },
        passwordRules:{
              password : [{required:true,trigger:'blur',validator: validatePassword}],
              mdfpassword : [{required:true,trigger:'blur',validator: validatemdfPassword}],
            },
        LicenseRules: {
            license : [{required:true,trigger:'blur',validator: validateLicense}],
        }
    }
    },
    methods : {
      submitUpload(file){
        this.Licenseform.license = file.raw
      //上传文件  
    },
    onSubmit(){
      //提交表单
      this.$refs.form.validate(valid => {
        if(valid){
          var pattern = /^([0-9a-zA-Z_\.\-\])+\@([0-9a-zA-Z_\.\-\])+\.([a-zA-Z]+)$/;
          if(this.form.user.mdfpassword != this.form.user.userpassword){
            ElMessage.error("两次输入的密码不同")
          }else if(!pattern.test(this.form.user.email)){
            ElMessage.error("邮箱格式错误，请重新输入")
          }else{
            this.form.supermark.address = this.$refs.addressSelect.data.cityObj.code //获取城市的区号
            console.info(this.$refs.upload);
            this.$store.dispatch('user/signUp',this.form)
            .then()
            .catch((err)=>{
            console.info(err)
            //提交表单异常
            // this.$router.push({path : '/message',query: '提交表单异常' })
          })
      }
        }
      })
      
      
    },
    async getCompanyList(){
        let companies = {}
        await CompanyList()
        .then((response) => {
          companies = response.data
        })
        .catch()
        this.companies = companies
      },
    async LoadUserMessage(){
        let user = {}, company = {}, supermark = {}
        await getMessage({token : this.$store.state.user.token}).then((response) =>{
            const resp = response.data
            user = resp.form.user,
            company = resp.form.company,
            supermark = resp.form.supermark
        })
        this.form.user = user
        this.form.company = company
        this.form.supermark = supermark
    },
    Preview(){
        // DownloadFile(this.form.supermark.license.url);
        window.open(this.form.supermark.license.url,'_blank')
    },
    async UpdatePassword(){
        this.$refs.passwordform.validate(async valid => {
            if(valid){
                //发送请求
                await UpdatePassword({token : this.$store.state.user.token, password : this.passwordform.password}).then((response) =>{
                    ElMessage({
                        message: response.message,
                        type : 'success'
                    })
                    this.LoadUserMessage()
                    this.PasswordFormVisible = false;
                }
                )
            }
        })
    },
    UpdateLicense(){
        this.$refs.Licenseform.validate(async valid => {
            if(valid){
                //发送请求
                const formData = new FormData()
                formData.append("token",this.$store.state.user.token)
                formData.append("license",this.Licenseform.license)
                await UpdateLicense(formData).then((response) =>{
                    ElMessage({
                        message: response.message,
                        type : 'success'
                    })
                    this.LoadUserMessage()
                    this.LicenseFormVisible = false;
                }
                )
            }
        })
    }
    }
}
</script>

<style scoped>
.user-container{
    padding-left:15px;
    padding-top: 15px;
    width: 80%;
    text-align: center;
}
</style>
